import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import xueshengList from '@/views/pages/xuesheng/list'
import xueshengDetail from '@/views/pages/xuesheng/formModel'
import xueshengAdd from '@/views/pages/xuesheng/formAdd'
import xueshengRegister from '@/views/pages/xuesheng/register'
import xueshengCenter from '@/views/pages/xuesheng/center'
import jiaoshiList from '@/views/pages/jiaoshi/list'
import jiaoshiDetail from '@/views/pages/jiaoshi/formModel'
import jiaoshiAdd from '@/views/pages/jiaoshi/formAdd'
import xiaoyuanzixunList from '@/views/pages/xiaoyuanzixun/list'
import xiaoyuanzixunDetail from '@/views/pages/xiaoyuanzixun/formModel'
import xiaoyuanzixunAdd from '@/views/pages/xiaoyuanzixun/formAdd'
import kechengleixingList from '@/views/pages/kechengleixing/list'
import kechengleixingDetail from '@/views/pages/kechengleixing/formModel'
import kechengleixingAdd from '@/views/pages/kechengleixing/formAdd'
import xueyechengjiList from '@/views/pages/xueyechengji/list'
import xueyechengjiDetail from '@/views/pages/xueyechengji/formModel'
import xueyechengjiAdd from '@/views/pages/xueyechengji/formAdd'
import zongheshixiangList from '@/views/pages/zongheshixiang/list'
import zongheshixiangDetail from '@/views/pages/zongheshixiang/formModel'
import zongheshixiangAdd from '@/views/pages/zongheshixiang/formAdd'
import yuanxixinxiList from '@/views/pages/yuanxixinxi/list'
import yuanxixinxiDetail from '@/views/pages/yuanxixinxi/formModel'
import yuanxixinxiAdd from '@/views/pages/yuanxixinxi/formAdd'
import kechengxinxiList from '@/views/pages/kechengxinxi/list'
import kechengxinxiDetail from '@/views/pages/kechengxinxi/formModel'
import kechengxinxiAdd from '@/views/pages/kechengxinxi/formAdd'
import saishichengjiList from '@/views/pages/saishichengji/list'
import saishichengjiDetail from '@/views/pages/saishichengji/formModel'
import saishichengjiAdd from '@/views/pages/saishichengji/formAdd'
import shehuishijianchengjiList from '@/views/pages/shehuishijianchengji/list'
import shehuishijianchengjiDetail from '@/views/pages/shehuishijianchengji/formModel'
import shehuishijianchengjiAdd from '@/views/pages/shehuishijianchengji/formAdd'
import zonghecepingList from '@/views/pages/zongheceping/list'
import zonghecepingDetail from '@/views/pages/zongheceping/formModel'
import zonghecepingAdd from '@/views/pages/zongheceping/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'xueshengList',
			component: xueshengList
		}, {
			path: 'xueshengDetail',
			component: xueshengDetail
		}, {
			path: 'xueshengAdd',
			component: xueshengAdd
		}
		, {
			path: 'xueshengCenter',
			component: xueshengCenter
		}
		, {
			path: 'jiaoshiList',
			component: jiaoshiList
		}, {
			path: 'jiaoshiDetail',
			component: jiaoshiDetail
		}, {
			path: 'jiaoshiAdd',
			component: jiaoshiAdd
		}
		, {
			path: 'xiaoyuanzixunList',
			component: xiaoyuanzixunList
		}, {
			path: 'xiaoyuanzixunDetail',
			component: xiaoyuanzixunDetail
		}, {
			path: 'xiaoyuanzixunAdd',
			component: xiaoyuanzixunAdd
		}
		, {
			path: 'kechengleixingList',
			component: kechengleixingList
		}, {
			path: 'kechengleixingDetail',
			component: kechengleixingDetail
		}, {
			path: 'kechengleixingAdd',
			component: kechengleixingAdd
		}
		, {
			path: 'xueyechengjiList',
			component: xueyechengjiList
		}, {
			path: 'xueyechengjiDetail',
			component: xueyechengjiDetail
		}, {
			path: 'xueyechengjiAdd',
			component: xueyechengjiAdd
		}
		, {
			path: 'zongheshixiangList',
			component: zongheshixiangList
		}, {
			path: 'zongheshixiangDetail',
			component: zongheshixiangDetail
		}, {
			path: 'zongheshixiangAdd',
			component: zongheshixiangAdd
		}
		, {
			path: 'yuanxixinxiList',
			component: yuanxixinxiList
		}, {
			path: 'yuanxixinxiDetail',
			component: yuanxixinxiDetail
		}, {
			path: 'yuanxixinxiAdd',
			component: yuanxixinxiAdd
		}
		, {
			path: 'kechengxinxiList',
			component: kechengxinxiList
		}, {
			path: 'kechengxinxiDetail',
			component: kechengxinxiDetail
		}, {
			path: 'kechengxinxiAdd',
			component: kechengxinxiAdd
		}
		, {
			path: 'saishichengjiList',
			component: saishichengjiList
		}, {
			path: 'saishichengjiDetail',
			component: saishichengjiDetail
		}, {
			path: 'saishichengjiAdd',
			component: saishichengjiAdd
		}
		, {
			path: 'shehuishijianchengjiList',
			component: shehuishijianchengjiList
		}, {
			path: 'shehuishijianchengjiDetail',
			component: shehuishijianchengjiDetail
		}, {
			path: 'shehuishijianchengjiAdd',
			component: shehuishijianchengjiAdd
		}
		, {
			path: 'zonghecepingList',
			component: zonghecepingList
		}, {
			path: 'zonghecepingDetail',
			component: zonghecepingDetail
		}, {
			path: 'zonghecepingAdd',
			component: zonghecepingAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/xueshengRegister',
		component: xueshengRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
